import React, {useRef,useState, useEffect} from 'react';
import {  Button } from "react-bootstrap"
import { useAuth } from "../contexts/AuthContext"
import { useHistory } from "react-router-dom"
import io from 'socket.io-client'; 
import './Dashboard.css'

export default function Dashboard() {
  const dlElement = useRef()
  const linkInput = useRef()
  const logs = useRef()
  const socket = useRef()
  
  const [error, setError] = useState("")
  const { currentUser, logout } = useAuth()
  const history = useHistory()
  
  useEffect(() => {
  
    if (socket.current == null) {
      socket.current = io("ws://130.162.44.9:5000", {  transports: ['websocket'] });
      // console.log(socket.current)
    }

       
    socket.current.on('addLog', function(filename) {
      
    var item = document.createElement('li');
    item.textContent = filename;
    logs.current.appendChild(item)
    
    })

    socket.current.on('downloadLink', function(filename) {
      download(filename)
      console.log(filename)
    })
    return () => {
      console.log('xd')
   };
  }, []);
  async function handleLogout() {
    setError("")

    try {
      await logout()
      history.push("/login")
    } catch {
      setError("Failed to log out")
    }
  }
  function submit(e) {
    e.preventDefault()
    socket.current.emit('submit',linkInput.current.value);
    
  
    linkInput.current.value = ""

  }
  function download(url) {
    
    dlElement.current.href=encodeURIComponent(url)
    dlElement.current.style.display ="block"
    dlElement.current.innerHTML = "download "+ url
    dlElement.current.click()
  }
  return (
    <>
          

          <form className="" onSubmit={submit} >
          <input  size="33" ref ={linkInput} autoFocus  />
          <button >Submit</button>
          </form>
          <a download="" ref={dlElement} style={{ display: "none" }}> download</a>
          <ul  ref={logs} style={{ listStyleType: "none",fontSize: "10px",left:'5px' }}>
           
          </ul>
          
          
        
      
          <div className="w-auto text-center mt-2">
        <Button variant="link" onClick={handleLogout}>
          Log Out
        </Button>
      </div>
    </>
  )
}
